import interfaces from '../../utils/interfaces.js'
export default {
	data() {
		return {
			goodsList: [], // 商品列表大数组
			filterby: "all", // 默认选择的tab
			page: 1,
			size: 6,
			loadingText: "正在加载....",
			filterByList: [{
					text: "全部",
					selected: true,
					filterby: "all"
				},
				{
					text: "口碑",
					selected: false,
					filterby: "public"
				},
				{
					text: "热门",
					selected: false,
					filterby: "hot"
				}
			]
		}
	},
	methods: {
		handleGoods(goods){
			// 页面跳转 商品详情
			uni.navigateTo({
				url:'./goods?goodsInfo='+JSON.stringify(goods)
			})
		},
		handleSelect(index) {
			this.filterByList[index].selected = true;

			// 其他的selected false
			for (let i = 0; i < this.filterByList.length; i++) {
				if (i != index) {
					this.filterByList[i].selected = false;
				}
			}
			
			// 数据请求
			this.filterby = this.filterByList[index].filterby;
			this.page = 1;
			this.loadingText = "加载中...";
			this.goodsList = [];
			this.loadData();
		},
		loadData() {
			this.request({
				url: `${interfaces.getGoodsList}/${this.filterby}/${this.page}/${this.size}`,
				success: ((res) => {
					if(res.data.length > 0){
						res.data.forEach(item => {
							this.goodsList.push(item);
						})
					}else{
						this.loadingText = "到底了";
					}
				})
			})
		}
	},
	onLoad(option) {
		// console.log(option);
		// 动态修改nav title
		uni.setNavigationBarTitle({
			title: option.name
		})

		// 加载数据
		this.loadData();
	},
	onPullDownRefresh(){
		setTimeout(() => {
			this.page = 1;
			this.loadingText = "加载中...";
			this.goodsList = [];
			this.loadData();
			uni.stopPullDownRefresh();
		},1000)
	},
	// 上啦加载
	onReachBottom(){
		this.page++;
		this.loadData();
	}
}
